import React from 'react'

import {
    Avatar,
    Button,
    Grid,
    Typography
} from '@material-ui/core'

import Item from '../components/Builds/Item'
import SelectedStat from '../components/Builds/SelectedStat'

import {
    StaticAbility
} from '../components/Builds/StaticAbility'

import { colors } from '../styles/theme'

const BuildView = (props) => {
    const {
        data,
        editAction
    } = props

    const maxAbilityLevel = 20
    const createdOn = new Date()
    const monthNames = ["JANUARY", "FEBRUARY", "MARCH", "APRIL", "MAY", "JUNE", "JULY", "AUGUST", "SEPTEMBER", "OCTOBER", "NOVEMBER", "DECEMBER"]

    return (
    <Grid container style={styles.rootContainer}>
        <Grid item xs={12}>
            <Typography>{data.myth.data.name.toUpperCase()} BUILD GUIDE {/* BY {data.author} */}</Typography>
        </Grid>
        <Grid container style={{ 
            minHeight: 250,
            backgroundImage: `url(${data.myth.data.background})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover' }}>
            <Grid container style={{ alignContent: 'center', alignItems: 'center', marginLeft: 10}}>
                <Grid item xs={1} >
                    <Avatar variant="square"
                        src={`${process.env.PUBLIC_URL}/${data.myth.role.image}`} />
                </Grid>
                <Grid item xs={11}>
                    <h2>{data.myth.title}</h2>
                </Grid>
                <Grid item xs={1} />
                <Grid item xs={11}>
                    <Typography>
                        CREATED ON {monthNames[createdOn.getMonth()]} {createdOn.getDate()}, {createdOn.getFullYear()}
                    </Typography>
                </Grid>
                { editAction &&
                <Grid item xs={12}>
                    <Grid container style={styles.buttons}>
                        <Grid item xs={1} />
                        <Grid item xs>
                            <Button style={styles.buttons.edit} onClick={editAction}>
                                {'< EDIT'}
                            </Button>
                        </Grid>
                        { /*
                        <Grid item xs>
                            <Button style={styles.buttons.publish}>
                                {'PUBLISH >'}
                            </Button>
                        </Grid>
                        */ }
                        <Grid item xs={8}>
                            
                        </Grid>
                    </Grid>
                </Grid>
                }
            </Grid>
        </Grid>
        
        <h2 style={styles.headerTitle}>ITEMS</h2>
        <Grid item xs={12}>
            { data.items.map((section, index) => 
                <Grid container>
                    <Grid container>
                        <Grid item xs={12}>
                            <h2 style={styles.items.title}>{section.title}</h2>
                        </Grid>
                        { section.build.map((item, itemIndex) => 
                            <Grid item xs={1}>
                                <Item item={item} />
                            </Grid>
                        )}
                    </Grid>
                    <Grid container style={styles.items.notes}>
                        <Grid item xs={12}>
                            <Typography>{section.notes}</Typography>
                        </Grid>
                    </Grid>
                </Grid>
            )}
        </Grid>
        <Grid item xs={12}>
            <h2 style={styles.headerTitle}>LOADOUT</h2>
        </Grid>
        <Grid item xs={12}>
            <h2>Combat Stats</h2>
        </Grid>
        <Grid container style={styles.statContainer}>
            { data.loadout.selectedStats.map((stat, _) => 
            <Grid item xs={3} style={styles.loadout.selectedStat}>
                <SelectedStat stat={stat} />
            </Grid>
            )}
        </Grid>

        <h2 style={styles.headerTitle}>ABILITY ORDER</h2>
        <Grid item xs={12}>
            <Grid container>
                <Grid item xs={6}>
                    <StaticAbility ability={data.abilities.data[2]} />
                </Grid>
                <Grid item xs={6}>
                    <StaticAbility ability={data.abilities.data[0]} />
                </Grid>
            </Grid>
            { data.abilities.data.slice(3, 8).map((ability, abilityIndex) => 
                <Grid container style={styles.abilities.dynamicAbilities}>
                    <Grid item xs={1}>
                        <img style={styles.abilities.image}
                            src={ability.imageURL}
                            alt={`ability ${ability.name}`} />
                    </Grid>
                    <Grid item xs={11}>
                        <Grid container>
                            <Grid item xs={12}>
                                <Typography style={styles.name}>
                                    {ability.type} - {ability.name}
                                </Typography>
                            </Grid>
                            { [...Array(maxAbilityLevel)].map((_, levelIndex) => 
                                <Button style={ (ability.type === "Special Ability" && levelIndex === 0) || ability.levels.includes(levelIndex+1) 
                                        ? styles.abilities.levelSelected 
                                        : styles.abilities.levelDefault
                                }
                                    disabled={true}
                                    key={`ability-${ability.name}-${levelIndex}-${ability.levels.includes(levelIndex+1)}`}>
                                    {levelIndex+1}
                                </Button>
                            )}
                        </Grid>
                    </Grid>
                </Grid>
            )}
            <Grid container style={styles.abilities.notes}>
                <Grid item>
                    {data.abilities.notes}
                </Grid>
            </Grid>
        </Grid>
    </Grid>
    )
}

export default BuildView

const styles = {
    rootContainer: {
        color: colors.theme.white
    },
    headerTitle: {
        color: colors.theme.goldPrimary,
    },

    buttons: {
        marginTop: 15,
        edit: {
            border: `1px solid ${colors.theme.goldPrimary}`,
            color: colors.theme.goldPrimary
        },
        publish: {
            border: `1px solid ${colors.theme.goldPrimary}`,
            backgroundColor: colors.theme.goldPrimary
        }
    },

    items: {
        title: {
            marginTop: 0
        },
        notes: {
            marginTop: 15,
            marginBottom: 15
        }
    },

    loadout: {
        selectedStat: {
            height: 75,
            marginTop: 5,
            marginBottom: 5,
            paddingRight: 10
          }
    },

    abilities: {
        dynamicAbilities: {
            marginTop: 15
        },
        image: {
            border: `1px solid ${colors.theme.goldPrimary}`,
            height: 75,
            width: 75
        },
        levelDefault: {
            backgroundColor: colors.theme.black,
            color: colors.theme.white,
            border: `1px solid ${colors.theme.goldPrimary}`,
            
            minWidth: 0,
            margin: 5,
            width: 35
        },
        levelSelected: {
            backgroundColor: colors.theme.goldPrimary,
            color: colors.theme.black,
            
            minWidth: 0,
            margin: 5,
            width: 35
        },
        notes: {
            marginTop: 15
        }
    },

    pairings: {

    }
}